<h3></h3>
<p> LoadingBar </p>
<div class="demo-block">
   <c-button @click="setSpeed">speed</c-button>
   <c-button @click="setSpinner">spinner</c-button>
   <c-button @click="setPercentNum">percentNum</c-button>
   <c-button @click="setShowSpinner">spinner</c-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><c-button @click="setSpeed">speed</c-button>
<c-button @click="setSpinner">spinner</c-button>
<c-button @click="setPercentNum">percentNum</c-button>
