</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">c-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setSpeed"</span>&gt;</span>speed<span class="hljs-tag">&lt;/<span class="hljs-name">c-button</span>&gt;</span>
