<h3>LoadingBar config</h3>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>speed</td>
<td></td>
<td>Number</td>
<td>0-100</td>
<td>5</td>
</tr>
<tr>
<td>easing</td>
<td>spinner</td>
<td>String</td>
<td>linear, ease, cubic-bezier...</td>
<td></td>
</tr>
<tr>
<td>percentNum</td>
<td></td>
<td>Float</td>
<td>0-1</td>
<td>Math.random()</td>
</tr>
<tr>
<td>showSpinner</td>
<td>spinner</td>
<td>Boolean</td>
<td>true, false</td>
<td>spinner</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default{
    mounted () {
      
    },
    methods: {
      start () {
        this.$loading.start()
      },
      end () {
        this.$loading.end()
      },
      error () {
        this.$loading.error()
      },
      setSpeed () {
        this.$loading.config({
          speed: 10
        })
        this.$loading.start()
      },
      setSpinner () {
        this.$loading.config({
          easing: 'ease'
        })
        this.$loading.start()
      },
      setPercentNum () {
        this.$loading.config({
          percentNum: 0.1
        })
        this.$loading.start()
      },
      setShowSpinner () {
        this.$loading.config({
          showSpinner: false
        })
        this.$loading.start()
      }
    }
  }
</script>


